.title {
	margin-bottom: 65px;
	text-align: center;

	&__header {
		margin: 0;
		font-size: 2.25rem;
		font-family: $ff-main-bold;
		line-height: 1;
	}

	&__text {
		font-size: 1.25rem;
		font-family: $ff-main-light;
		margin: 12px 0;
	}

	&--with-bottom-line {
		position: relative;

		&:after {
			@include centerer-x();

			content: '';
			bottom: -16px;
			width: 57px;
			height: 4px;
			background: #424242;
		}
	}
}