.about {
	padding: 99px 0 105px 0;
	text-align: center;

	&__list {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		margin: 0;
		padding: 0 50px;
	}

	&__item {
		width: 25%;
	}

	&__icons {
		height: 140px;
	}

	&__header {
		margin: 12px 0 12px 0;
		font-size: 1.375rem;
		font-family: $ff-main-bold;
		color: #424242;
	}

	&__description {
		margin-top: 0;
		font-size: 0.875rem;
		line-height: 1.5;
		color: #5c5c5c;
		text-align: justify;
	}
}